.header {
    margin-bottom: 2vw;
    &__logo {
        height: 5vw;
    }
    &__flex {
        display: flex;
    }
    &__flex-space {
        justify-content: space-between;
        @media (max-width: 500px) {
            flex-wrap: wrap;
        }
    }
    &__map {
        height: 60px;
    }
    &__flex-map {
        display: flex;
        cursor: pointer;
    }
    &__map::before {
        background-image: url("../../assets/images/header/icon.png");
        background-repeat: no-repeat;
        background-position: center;
        height: 6vw;
        width: 6.2vw;
    }
    &__img {
        background-image: url("../../assets/images/header/icon.png");
        background-repeat: no-repeat;
        background-position: center;
        height: 6vw;
        width: 6.2vw;
    }
    &__button {
        background-color: #313d4b;
        color: white;
        border: none;
        border-radius: 5px;
        padding: 0 30px;
        height: 50px;
        font-family: UniNeueRegular;
        font-size: 14px;
        @media (max-width: 500px) {
            padding: 0 5px;
        }
    }
    &__text {
        width: 200px;
        font-size: 14px;
        color: #273b4a;
        margin-left: 10px;
        @media (max-width: 992px) {
            display: none;
        }
    }
    &__texti {
        margin-left: 10px;
        font-size: 14px;
        padding: 15px 0;
        color: #273b4a;
        @media (max-width: 992px) {
            display: none;
        }
    }
    &__bold {
        font-family: UniNeueBlack;
    }
    &__light {
        font-family: UniNeueBook;
    }
    &__icon {
        height: 3vw;
        width: 3vw;
        margin: 5px;
    }
    &__phone {
        font-size: 14px;
    }
}
.transition--slide {
    position: relative;
    overflow: hidden;
}

.transition--slide::before {
    margin-left: 60px;
    content: " ";
    width: 30px;
    height: 300px;
    background: #ffffff80;
    animation-delay: 0.05s;
    position: absolute;
    left: -40px;
    top: -150px;
    animation-timing-function: ease-in-out;
    transition: all 0.1s;
    animation-name: slideme;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    transform: rotate(45deg);
}

@keyframes slideme {
    0% {
        left: -30%;
    }
    100% {
        left: 100%;
    }
}

@keyframes pulse {
    0% {
        transform: scale(1);
    }
    70% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}
.Toggle__display {
    display: none;
    @media (max-width: 992px) {
        display: block;
    }
}
