.calculater {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    width: 60vw;
    @media (max-width:500px){
        justify-content: center;
        flex-direction: column;
        width: 80vw;
    }
    &__btn {
        border: none;
        margin: 0;
    }
    &__img {
        margin-top: 20px;
        height: 80px;
    }
    &__arrow {
        height: 20px;
        margin-top: 47px;
        margin-left: 6px;
    }
    &__flex {
        display: flex;
        width: 100%;
        margin-top: 10px;
        justify-content: space-between;
    }
}
.calculater div {
    width: 50 vw;
    @media (max-width:500px){
        width: 90vw;
    }
}
.calculater div :last-child {
    width: 30 vw;
}
.calculater h5,
h6 {
    display: inline;
    line-height: 1.7;
    font-size: 16px;
    padding-left: 5%;
    margin-bottom: 20px;
}
.calculater h5 h6:last-child {
    background-color: white;
    border-radius: 5px;
    padding: 3px 7px;
}
.calculater button {
    border: none;
    float: left;
    background-image: url("../../assets/images/calculater/calcbtn.png");
    background-size: cover;
    height: 5vw;
    width: 25vw;
    margin-top: 20px;
}
.transition--slide {
    position: relative;
    overflow: hidden;
}

.transition--slide::before {
    margin-left: 60px;
    content: " ";
    width: 30px;
    height: 300px;
    background: #ffffff80;
    animation-delay: 0.05s;
    position: absolute;
    left: -40px;
    top: -150px;
    animation-timing-function: ease-in-out;
    transition: all 0.1s;
    animation-name: slideme;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    transform: rotate(45deg);
}

@keyframes slideme {
    0% {
        left: -30%;
    }
    100% {
        left: 100%;
    }
}

@keyframes pulse {
    0% {
        transform: scale(1);
    }
    70% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}
