.popup {
    width: 100%;
    min-height: 100%;
    background-color: rgba(61, 58, 58, 0.329);
    font-size: 24px;
    overflow: hidden;
    position: fixed;
    top: 0px;
    z-index: 1;
  
    &__contentSlider {
      margin: 5%;
      background:none;
      border: none;
      // box-shadow: 0px 0px 10px #000;
      position: relative;
    }
  }
  .close {
    border-radius: 40px;
    z-index: 2;
    cursor: pointer;
    position: absolute;
    bottom: 10px;
    right: 40px;
    top: 10px;
  }
  
  .close:before {
    content: "+";
    color: #111111;
    position: fixed;
    z-index: 2;
    transform: rotate(45deg);
    font-size: 50px;
    line-height: 1;
    transition: all 0.3s cubic-bezier(0.77, 0, 0.2, 0.85);
  }
  
  .close:hover:before {
    transform: scale(1.2) rotate(45deg);
    color: #111111;
  }
  