.page {
    width: 60%;
    &__title {
        font-size: 48px;
        font-family: UniNeueBold;
        line-height: 1.2;
        @media (max-width: 992px) {
            font-size: 36px;
        }
        @media (max-width: 500px) {
            font-size: 24px;
        }
    }
    @media (max-width: 500px) {
        width: 9
        0vw;
    }
    &__titles {
        font-family: UniNeueBook;
    }
    &__flex-space {
        display: flex;
        justify-content: space-between;
        margin-top: 20px;
        @media (max-width: 500px) {
            justify-content: center;
            flex-direction: column;
        }
    }
    &__flex {
        display: flex;
        @media (max-width: 500px) {
            width: 90vw;
        }
    }
    &__flex:last-child {
        width: 45vw;
        @media (max-width: 500px) {
            width: 90vw;
        }
    }
    &__flex::before {
        position: relative;
        transform: rotate(30deg);
        font-size: 28px;
        content: "/";
        color: orangered;
        font-family: UniNeueBlack;
        font-weight: bold;
        right: 30px;
    }
    &__text {
        padding: 12% 0;
        margin-right: 2vw;
    }
    &__text h4,
    h5 {
        display: inline;
    }
}
